export const groupUsers = users => {
  const groupedUsers = {};
  users.forEach(user => {
    const {guid, location, ...rest} = user;
    if (location in groupedUsers) {
      groupedUsers[location][guid] = rest;
    } else {
      groupedUsers[location] = {[guid]: rest};
    }
  });
  return groupedUsers;
};

export const getDepartments = users => {
  const departments = {};
  users.forEach(user => {
    if (!(user.department in departments)) {
      departments[user.department]=false;
    }
  });
  return departments;
}

const applyDepartmentFilter = (users, storeFilter) => {
  return Object.keys(users).filter(guid => {
    const department = users[guid].department;
    return (storeFilter.selectedDepartments[department]);
  });
};

export const makeRandomPairings = (users, filter) => {
  const pairings = {};
  const giverUserIds = applyDepartmentFilter(users, filter);
  const receiverUserIds = [...giverUserIds].sort((a,b) => .5 - Math.random());
  while (giverUserIds.length) {
    const giverId = giverUserIds.shift();
    const receiverId = (receiverUserIds[0] === giverId) ? receiverUserIds.pop() : receiverUserIds.shift();
    pairings[giverId] = receiverId;
  };
  return pairings;
}
