import React from "react";
import { Toolbar, SelectLabel, SectionContainer, Filter } from "./Toolbar.styles";

class ToolbarSection extends React.Component {
  onCityChange = ({ target: { value }}) => {
    this.props.selectCity(value);
  };

  handleFilterDept = ({ target: { value }})=> {
    console.log("handle dept: ", value);
    this.props.changeDeptSelection(value);
  }

  renderSelectOption = city => (
    <option key={city} value={city}>{city}</option>
  );

  renderCheckbox = dept => {
    const selected = this.props.departments[dept];
    return (
      <Filter key={dept}>
        <input type="checkbox" value={dept} checked={selected} onChange={this.handleFilterDept}/>
        {dept}
      </Filter>
    );
    }

  render = () => (
    <Toolbar>
      <SectionContainer>
        <SelectLabel>FILTER: </SelectLabel>
        <Filter>
          <select onChange={this.onCityChange} value={this.props.selectedCity}>
            {this.props.cities.map(this.renderSelectOption)}
          </select>
        </Filter>
        {Object.keys(this.props.departments).map(this.renderCheckbox)}
      </SectionContainer>
    </Toolbar>
  );
}

export default ToolbarSection;