import fetchUsers from '../api/usersAPI';
import * as types from './actionTypes';
import { groupUsers, makeRandomPairings, getDepartments } from './helpers';

export const loadUsers = () => (dispatch, getState) => {
  return fetchUsers().then(usersServerData => {
    const regroupedUsers = groupUsers(usersServerData.users);
    const departments = getDepartments(usersServerData.users);
    dispatch(loadUsersSuccess(regroupedUsers, departments));
    dispatch(selectCity(getState().filter.selectedCity));
  }).catch(error => {
    throw(error);
  })
};

const loadUsersSuccess = (users, departments) => (
  {
    type: types.LOAD_USERS_SUCCESS,
    users,
    departments
  }
);

export const selectCity = city => (dispatch, getState) => { 
  const newFilter = {
    ...getState().filter,
    selectedCity: city,
  };
  dispatch(makePairings(getState().users[city], newFilter));
  dispatch({
    type: types.UPDATE_FILTER,
    filter: newFilter
  });
}

export const changeDeptSelection = department => (dispatch, getState) => { 
  console.log('selected dept: ', department);
  const state = getState();
  const { selectedDepartments } = state.filter;
  const newFilter = {
  ...state.filter,
    selectedDepartments: {...selectedDepartments, [department]: !selectedDepartments[department]}
  };
  console.log("new filter: ", newFilter);
  const selectedCity = state.filter.selectedCity;
  dispatch({
    type: types.UPDATE_FILTER,
    filter: newFilter
  });
  dispatch(makePairings(state.users[selectedCity], newFilter));
  console.log("filter after: ", state.filter);

}

const makePairings = (users, newFilter) => {
  return {
    type: types.MAKE_PAIRINGS,
    pairings: makeRandomPairings(users, newFilter)
  };
}
